import * as React from 'react';
import PropTypes from 'prop-types';
import Button from '@mui/material/Button';
import { styled } from '@mui/material/styles';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogContent from '@mui/material/DialogContent';
import DialogActions from '@mui/material/DialogActions';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';
import Typography from '@mui/material/Typography';
import './Note.css'

const BootstrapDialog = styled(Dialog)(({ theme }) => ({
    '& .MuiDialogContent-root': {
        padding: theme.spacing(2),
    },
    '& .MuiDialogActions-root': {
        padding: theme.spacing(1),
    },
}));

function BootstrapDialogTitle(props) {
    const { children, onClose, ...other } = props;

    return (
        <DialogTitle sx={{ m: 0, p: 2 }} {...other}>
            {children}
            {onClose ? (
                <IconButton
                    aria-label="close"
                    onClick={onClose}
                    sx={{
                        position: 'absolute',
                        right: 8,
                        top: 8,
                        color: (theme) => theme.palette.grey[500],
                    }}
                >
                    <CloseIcon />
                </IconButton>
            ) : null}
        </DialogTitle>
    );
}

BootstrapDialogTitle.propTypes = {
    children: PropTypes.node,
    onClose: PropTypes.func.isRequired,
};

export default function Edit({edit, note, setnotes}) {
    const [open, setOpen] = React.useState(false);

    const handleClickOpen = () => {
        setOpen(true);
    };
    const handleClose = () => {
        setOpen(false);
    };
    const updatehndle = () => {
        const Noteedit = note.map((elm) => {
            if (edit === elm.id) {
               return(
                {
                    ...elm, title:document.getElementById("editttl").value,
               disc:document.getElementById("disc").value
                }
               )
                
            }else{
                    return elm
                }
        })
        setnotes(Noteedit)
        console.log(Noteedit)
        console.log(edit)
    }
    

    return (
        <div>

            <i class=" fa fa-duotone fa-pen-to-square" variant="outlined" onClick={handleClickOpen}></i>

            <BootstrapDialog
                onClose={handleClose}
                aria-labelledby="customized-dialog-title"
                open={open}
            >
                <BootstrapDialogTitle id="customized-dialog-title" onClose={handleClose}>
                    <input className='formlabel2' id="editttl"></input>

                </BootstrapDialogTitle>
                <DialogContent dividers>
                    <textarea name="Notes" id="disc" cols="7" rows="7" className='formlabel1'  ></textarea>
                </DialogContent>
                <DialogActions>
                    <Button autoFocus  sx={{ backgroundColor: 'rgb(176, 225, 245)' }} onClick={updatehndle} >
                        Edit
                    </Button>
                </DialogActions>
            </BootstrapDialog>
        </div>
    );
}